export default "header {\n  width: 100%;\n  height: 7em;\n  padding-top: 0.3em;\n  border-top: 4px solid black;\n  background-color: #5d0c1d;\n}\n\n/* img {\n  width: 406px;\n  height: 104px;\n} */\n\n.flex-container {\n  display: flex;\n  justify-content: center;\n}\n\n/* The navigation menu */\n\n.navbar {\n  /* overflow: hidden; */\n  background-color: #333;\n  border-top: 4px solid black;\n  border-bottom: 4px solid black;\n  justify-content: space-evenly;\n  z-index: 999;\n}\n\n/* Navigation links */\n\n.navbar a {\n  float: left;\n  font-size: 16px;\n  color: #ffffff;\n  text-align: center;\n  padding: 14px 16px;\n  text-decoration: none;\n  z-index: 999;\n}\n\n/* a,\nbutton {\n  list-style-type: none;\n  display: none;\n} */\n\n/* The subnavigation menu */\n\n.subnav {\n  position: relative;\n  display: inline-block;\n}\n\n/* Subnav button */\n\n.subnav .subnavbtn {\n  font-size: 16px;\n  border: none;\n  outline: none;\n  color: #ffffff;\n  padding: 14px 16px;\n  background-color: inherit;\n  font-family: inherit;\n  margin: 0;\n}\n\n.navbar a:hover,\n.subnav:hover .subnavbtn {\n  background-color: #d6d6d6;\n  color: black;\n}\n\n/* Style the subnav content */\n\n.subnav-content {\n  display: none;\n  overflow: hidden;\n  margin-left: 0px;\n  background-color: #d6d6d6;\n  color: black;\n  position: absolute;\n  min-width: 160px;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n}\n\n/* Style the subnav links */\n\n.subnav-content a {\n  float: left;\n  width: 100%;\n  background-color: #d6d6d6;\n  color: black;\n  text-align: left;\n  text-decoration: none;\n}\n\n/* Add a dark background color on hover */\n\n.subnav-content a:hover {\n  background-color: #333;\n  text-align: left;\n  width: 100%;\n  color: #ffffff;\n}\n\n.hover-state {\n  display: block;\n}\n\n/* .navbar-toggle {\n  position: absolute;\n  top: 10px;\n  left: 20px;\n  cursor: pointer;\n  font-size: 24px;\n}\n\n.active {\n  display: block;\n}\n\nfa-bars {\n  color: #d6d6d6;\n  justify-self: center;\n} */\n\n/*# sourceMappingURL=data:application/json;base64, */"